// dependencies
import { showLoader, hideLoader, __html, html, initHeader, initFooter, initBreadcrumbs, parseApiError, onClick, spaceID, toast, onKeyUp, H, getAPI, link } from '@kenzap/k-cloud';
import { getCookie, getPageNumber } from "../_/_helpers.js"
import { HTMLContent } from "../_/_cnt_home.js"

// where everything happens
const _this = { 

    state: {
        firstLoad: true,
        html: '',
        data: {},
        ajaxQueue: 0,
        limit: 25,
        responseKey: [],
        modal: null,
        modalCont: null,
        editor: {},
    },
    init: () => {
        
        _this.getData();
    
    },
    getData: () => {

        // show loader during first load
        if (_this.state.firstLoad) showLoader();

        // do API query
        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    // keys: {
                    //     type:       'api-key',
                    //     keys:       ['private']
                    // },
                    locale: {
                        type:       'locale',
                        id:         getCookie('lang')
                    },
                    mydata: {
                        type:       'mydata',
                    },
                    // dashboard: {
                    //     type:       'dashboard',
                    // }
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){

                // init header
                initHeader(response);

                // get core html content 
                _this.loadHomeStructure();  

                // render table
                _this.renderPage(response);

                // listeners
                _this.initListeners();

                // initiate footer
                _this.initFooter();

                // first load
                _this.state.firstLoad = false;


            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    },
    getExtensions: () => {
        
        // console.log('getQRHTML');

        // init params
        let params = new URLSearchParams();
        params.append("cmd", "preview_extensions");
        params.append("s", '');
        params.append("token", getCookie('kenzap_token'));

        // do API query
        fetch('https://siteapi.kenzap.cloud/v1/', {
            method: 'post',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/x-www-form-urlencoded',
            },
            body: params
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){
            
                // console.log(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }, 
    renderPage: (response) => {

        // initiate breadcrumbs
        initBreadcrumbs(
            [
                { link: link('https://dashboard.kenzap.cloud?launcher=mydata'), text: __('Dashboard') },
                { text: __('My Data') },
            ]
        );

        // _this.state.dashboard = response.dashboard;

        /**
         * ---------------------------------------
         * This demo was created using amCharts 4.
         *
         * For more information visit:
         * https://www.amcharts.com/
         *
         * Documentation is available at:
         * https://www.amcharts.com/docs/v4/
         * ---------------------------------------
         * 
         * Examples
         * https://www.amcharts.com/demos/collapsible-force-directed-tree/
         * https://codepen.io/team/amcharts/pen/vYBPJNy
         */

        am4core.useTheme(am4themes_animated);

        let chart = am4core.create("mydata-chart", am4plugins_forceDirected.ForceDirectedTree);
        // chart.legend = new am4charts.Legend();
        let networkSeries = chart.series.push(new am4plugins_forceDirected.ForceDirectedSeries())
        let myData = [];
        let ti = 0;
        // console.log(response);

        // get parent nodes
        let parentNodes = {};
        response.mydata.data.forEach((el, i) => {
            
            let key_parts = el.key.split('-');
            if(key_parts.length > 1){

                // let key_start = key_parts.map((s,i) => i < key_parts.length - 1 ? s : "").join('-');
                // let key_end = key_parts[key_parts.length-1];
                let key_start = key_parts[0];

                if(!parentNodes[key_start]){ parentNodes[key_start] = el.count; }else{ parentNodes[key_start] += el.count; }
            }else{

                if(!parentNodes[el.key]){ parentNodes[el.key] = el.count}else{ parentNodes[el.key] += el.count; }
            }
        });

        // console.log(parentNodes);

        Object.entries(parentNodes).forEach((el, i) => {

            // console.log(el);

            // let slug = el[0].split('-');
            let key_parts = el[0].split('-');
            // let key_start = key_parts.map((s,i) => i < key_parts.length - 1 ? s : "").join('-');
            let key_start = key_parts[0];

            let children = [];
            response.mydata.data.forEach((elc, i) => {

                if(elc.key.startsWith(key_start+'-')){

                    children.push({name: elc.key + ' ' + elc.count, value: 5});
                }
            });

            children.push({name: el[0] + ' ' + el[1], value: 5});

            myData.push({
                name: el[0],
                value: 8,
                // fixed: true,
                // x: am4core.percent(10+(i*20)),
                // y: am4core.percent(50),  
                children: children,
            });
        });

        // console.log(myData);

        networkSeries.data = myData;

        networkSeries.dataFields.linkWith = "linkWith";
        networkSeries.dataFields.category = "category";
        networkSeries.dataFields.name = "name";
        networkSeries.dataFields.id = "name";
        networkSeries.dataFields.value = "value";
        networkSeries.dataFields.children = "children";
        networkSeries.dataFields.fixed = "fixed";

        networkSeries.nodes.template.propertyFields.x = "x";
        networkSeries.nodes.template.propertyFields.y = "y";

        networkSeries.nodes.template.tooltipText = "{name}";
        networkSeries.nodes.template.fillOpacity = 1;

        networkSeries.nodes.template.label.text = "{name}"
        networkSeries.fontSize = 8;
        networkSeries.maxLevels = 3;
        networkSeries.nodes.template.label.hideOversized = true;
        networkSeries.nodes.template.label.truncate = true;
        networkSeries.nodes.template.draggable = true;
        networkSeries.nodePadding = 10;

        networkSeries.nodes.template.events.on("hit", function(ev){

            let circle = ev.target.label.currentText.split(" ");

            _this.state.modalCont = null;

            // console.log(ev.target);
            if(circle.length>1) _this.getKeyData(circle[0]);

        }, this);

        hideLoader();
    },
    getKeyData: (key) => {

        let s = document.querySelector('.input-search') ? document.querySelector('.input-search').value : "";

        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    data: {
                        type:       'find',
                        key:         key,
                        fields:     '*',
                        search:     {                                                           // if s is empty search query is ignored
                            s: s
                        },
                        // term: {
                        //     type:       'string',
                        //     field:      'ReceiptNumber',
                        //     value:       '',
                        //     relation:   _this.state.paid == "Paid" ? '!=' : '='
                        // },
                        sortby: {
                            field: 'updated',
                            order: 'DESC'
                        },
                        offset:     s.length > 0 ? 0 : getPageNumber() * _this.state.limit - _this.state.limit,
                        limit:      _this.state.limit,
                    }
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){

                _this.state.responseKey = response;

                _this.viewDataSets(key, response);

                // console.log(response);
            }
        })
        .catch(error => { parseApiError(error); });
    },
    initListeners: () => {

        // prevents listeners to be assigned twice
        if(!_this.state.firstLoad) return;

        // add layout listener
        onClick('.btn-publish', _this.listeners.publish);

        // domain change listener
        onClick('.domain-list li a', _this.listeners.domainChange);
        
        // add product confirm
        onClick('.btn-modal', _this.listeners.modalSuccessBtn);
    },
    listeners: {

        modalSuccessBtn: (e) => {
            
            _this.listeners.modalSuccessBtnFunc(e);
        },
        modalSuccessBtnFunc: null
    },
    viewDataSets: (key, response) => {

        _this.state.key = key;

        if(!_this.state.modalCont || !_this.state.modal){

            _this.state.modal = document.querySelector(".modal");
            _this.state.modalCont = new bootstrap.Modal(_this.state.modal);
            history.pushState({pageID: 'Data'}, 'Data', window.location.pathname + window.location.search + "#editing");
            _this.state.modal.querySelector(".modal-dialog").classList.add('modal-fullscreen');

            // header
            _this.state.modal.querySelector(".modal-header .modal-title").innerHTML = `
            <div class="d-flex align-items-center">
                <input type="text" style="width:320px;" placeholder="${ __html('Search in %1$', key) }" class="form-control fs-6 border-top-0 border-start-0 input-search border-end-0 rounded-0 ms-2" aria-label="${ __html('Search') }" aria-describedby="inputGroup-sizing-sm" >
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="ms-2 bi bi-list po restart-table d-none" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"/>
                </svg>
            </div>
            `;

            // buttons
            _this.state.modal.querySelector('.modal-footer').innerHTML = `
                <button type="button" class="btn btn-primary btn-modal btn-update-record d-none">${ __html('Update') }</button>
                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">${ __html('Close') }</button>
            `;

            // search records
            onKeyUp('.input-search', e => {

                e.preventDefault();

                if(_this.state.timeout) clearTimeout(_this.state.timeout);

                _this.state.timeout = setTimeout((_this) => { _this.getKeyData(key); }, 600, _this);
            });

            // rever from editing back to table view
            _this.state.modal.querySelector('.restart-table').addEventListener('click', e => {

                _this.state.modal.querySelector('.restart-table').classList.add('d-none');
                _this.state.modal.querySelector('.btn-update-record').classList.add('d-none');
                _this.getKeyData(key);
            });

            // save changes
            _this.state.modal.querySelector('.btn-update-record').addEventListener('click', e => {
                
                // console.log(JSON.parse(_this.state.editor['json'].getValue()));

                // send data
                fetch(getAPI(), {
                    method: 'post',
                    headers: H(),
                    body: JSON.stringify({
                        query: {
                            record: {
                                type:       'update',
                                key:        _this.state.key,
                                id:         _this.state._id,         
                                data:       JSON.parse(_this.state.editor['json'].getValue())
                            }
                        }
                    }) 
                })
                .then(response => response.json())
                .then(response => {

                    if (response.success){

                        toast( __html('Record updated') );

                        _this.state.modal.querySelector('.restart-table').classList.add('d-none');
                        _this.state.modal.querySelector('.btn-update-record').classList.add('d-none');
                        _this.getKeyData(key);

                    }else{

                        parseApiError(response);
                    }
                })
                .catch(error => { parseApiError(error); });
            });
        }

        // body
        _this.state.modal.querySelector(".modal-body").innerHTML = `
            <div class="row">
                <div class="col-sm-12">
                    <div id="editor" class="editor d-none" style="min-height:2000px;">


                    </div>
                    <div class="table-responsive">
                        <table class="table table-hover table-borderless align-middle table-striped table-p-list">
                            <thead>
                                <tr>
                                    <th id="table-id-header">${ __html("_id") }</th>
                                    <th>${ __html("data") }</th>
                                </tr>
                            </thead>
                            <tbody>
                            ${
                                response.data.length == 0 ? `<tr><td colspan="2" class="text-muted">${ __html('no data to display') }</td>` : ``
                            }

                            ${
                                response.data.map((obj, i) => {

                                    return `
                                    <tr>
                                        <td id="table-id${i}" class="table-obj table-id font-monospace">${ obj._id }</td>
                                        <td id="table-obj${i}" data-id="${ obj._id }" class="elipsized table-data font-monospace table-obj" style="">${ html(JSON.stringify(obj)) }</td>
                                    </tr>
                                    `;
                                    
                                }).join('')
                            }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        `;

        _this.state.modalCont.show();

        // init editor
        [...document.querySelectorAll('.table-data')].forEach((row, i) => {

            row.addEventListener('click', e => {

                e.preventDefault();

                let obj = _this.state.responseKey.data.filter(el => el._id == e.currentTarget.dataset.id)[0];

                document.querySelector('.restart-table').classList.remove('d-none');
                document.querySelector('#editor').classList.remove('d-none');
                document.querySelector('.table-p-list').classList.add('d-none');
                document.querySelector('.btn-update-record').classList.remove('d-none');

                _this.state._id = e.currentTarget.dataset.id;
                
                _this.state.editor['json'] = ace.edit("editor");
                ace.config.set('basePath', 'https://account.kenzap.com/js/ace/');
                // _this.state.editor['json'] .setTheme("ace/theme/monokai-light");
                _this.state.editor['json'].getSession().setMode("ace/mode/json");
                _this.state.editor['json'].setValue(JSON.stringify(obj, null, 2), -1);
            });
        });

        if(!_this.state.modalCont || !_this.state.modal){

        };
    },
    loadHomeStructure: () => {

        if(!_this.state.firstLoad) return;

        // get core html content 
        document.querySelector('#contents').innerHTML = HTMLContent(__);
    },
    initFooter: () => {
        
        initFooter(__('My Data 1.0.1 by %1$Kenzap%2$. ❤️ Licensed %3$GPL3%4$.', '<a class="text-muted" href="https://kenzap.com/" target="_blank">', '</a>', '<a class="text-muted" href="https://github.com/kenzap/my-data" target="_blank">', '</a>'), '');
    }
}

_this.init();
